@import url(https://fonts.googleapis.com/css2?family=Libre+Baskerville&display=swap);
iframe{display:none}*{box-sizing:border-box;font-family:'Libre Baskerville', serif}:root{width:-webkit-max-content;width:max-content;margin:40px auto;background-color:#8FBC8F;padding:20px;border-radius:20px}.App form{display:flex;justify-content:space-between}.field input,.field select{padding:0 0 0 20px;margin-bottom:10px;height:40px;width:100%;border-radius:20px;border:2px #FF8C00 solid;background-color:#20B2AA;color:#FF8C00;font-family:'Libre Baskerville', serif;text-decoration:none;text-shadow:1px 0 0 #000;transition:0.3s;outline:none}.field input:focus,.field select:focus{background-color:#FF8C00;color:#20B2AA;border:2px #20B2AA solid}.App form button{border:2px solid currentColor;height:100px;width:120px;border-radius:30px;margin-left:20px;color:#FF8C00;text-shadow:1px 0 0 #000;font-size:40px;overflow:hidden;position:relative;text-decoration:none;transition:0.2s -webkit-transform ease-in-out;transition:0.2s transform ease-in-out;transition:0.2s transform ease-in-out, 0.2s -webkit-transform ease-in-out;will-change:transform;z-index:0;background-color:#20B2AA}.App form button:after{background-color:#FF8C00;border-radius:30px;content:'';display:block;height:100%;width:100%;position:absolute;left:0;top:0;-webkit-transform:translate(-100%, 0) rotate(10deg);transform:translate(-100%, 0) rotate(10deg);-webkit-transform-origin:top left;transform-origin:top left;transition:0.2s -webkit-transform ease-out;transition:0.2s transform ease-out;transition:0.2s transform ease-out, 0.2s -webkit-transform ease-out;will-change:transform;z-index:-1}.App form button:hover::after{-webkit-transform:translate(0, 0);transform:translate(0, 0)}.App form button:hover{border:2px solid transparent;color:#20B2AA;-webkit-transform:scale(1.05);transform:scale(1.05);will-change:transform}.error{color:red;font-size:10px;font-style:italic}.TodoInfo{width:-webkit-max-content;width:max-content;padding:8px;margin:12px 0;border:1px solid #000;border-radius:8px;background-color:antiquewhite}.TodoInfo__title{margin:4px 0;font-size:inherit;color:#f00}.TodoInfo--completed .TodoInfo__title{color:#080}.UserInfo{font-style:italic}

